.navbar{
    display: flex;
    justify-content:space-around;
    padding: 16px;
    box-shadow: 0 1px 3px -2px black;
}
.nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
}
.nav-logo p{
    color: rgb(12, 45, 106);
    font-size: 38px;
    font-weight: 600;

}
.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap:70px;
    color:black;
    font-size: 50px;
}
.nav-menu li{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
}
.nav-menu hr{
    border: none;
    width: 80%;
    height:3px;
    border-radius: 10px;
    background: rgb(95, 95, 212);
}